<template>
    <div class="sidenav">
        <el-menu 
        :default-active="navselected" 
        :active="navselected" 
        class="el-menu" 
        @select="selectItems"
        :default-openeds="openends" 
        router>
            <el-submenu index="1">
                <template slot="title">
                    <span class="group-title"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item @click.native="clickLink('/home')" index="/home"></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2">
                <template slot="title">
                    <span class="group-title"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item @click.native="clickLink('/issue')" index="/issue"></el-menu-item>
                    <el-menu-item @click.native="clickLink('/product_list')" index="/product_list"></el-menu-item>
                    <el-menu-item @click.native="clickLink('/product_classify')" index="/product_classify"></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
                <template slot="title">
                    <span class="group-title"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item @click.native="clickLink('/order_list')" index="/order_list"></el-menu-item>
                    <el-menu-item @click.native="clickLink('/freight_template')" index="/freight_template"></el-menu-item>
                    <el-menu-item @click.native="clickLink('/address')" index="/address"></el-menu-item>
                    <el-menu-item @click.native="clickLink('/returnAddress')" index="/returnAddress"></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
                <template slot="title">
                    <span class="group-title"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item @click.native="clickLink('/shopInfo')" index="/shopInfo"></el-menu-item>
                </el-menu-item-group>
            </el-submenu>  
            <!-- <el-submenu index="5">
                <template slot="title">
                    <span class="group-title"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="5-1"></el-menu-item>
                </el-menu-item-group>
            </el-submenu>    -->
            <el-submenu index="6">
                <template slot="title">
                    <span class="group-title"></span>
                </template>
                <el-menu-item-group>             
                    <el-menu-item @click.native="clickLink('/my_account')" index="/my_account"></el-menu-item>
                    <el-menu-item @click.native="clickLink('/bank_step')" index="/bank_step"></el-menu-item>
                    <!-- <el-menu-item index="/commission_list"></el-menu-item> -->
                </el-menu-item-group>
            </el-submenu>                     
        </el-menu>
    </div>
</template>

<script>
    import { Menu,Submenu,MenuItem,MenuItemGroup } from 'element-ui'
    export default {
        components:{
            'el-menu':Menu,
            'el-submenu':Submenu,
            'el-menu-item':MenuItem,
            'el-menu-item-group':MenuItemGroup
        },
        data () {
            return {
                navselected:"/",
                openends:['1','2','3','4','5','6'],//menu
            }
        },
        // created () {
        //     this.$store.state.adminleftnavnum="/"; //2-2 active
        // },
        mounted(){
            // console.log(this.navselected)   
        },
        methods:{
            clickLink(path) {
                this.$router.push({
                    path,
                    query: {
                    t: +new Date() //queryview
                    }
                })
            },
            getNavType(){
                this.navselected=this.$store.state.adminleftnavnum;
                //store.state.adminleftnavnumnavselected
            },
            selectItems(index){
                // console.log(index)
                this.$store.state.adminleftnavnum=index;
               //indexstore
            }
        },
        watch: {
            // store.state
            '$store.state.adminleftnavnum': 'getNavType'
        }
    }
</script>
<style scoped>
.sidenav{
    background:#fff;
    max-width:140px;
}
.el-menu{
    max-width:200px;
    padding-top:15px;
}
.group-title{
    display:block;
    font-size:14.5px;
    color:#222;
}
.el-submenu__title{
    height:40px !important;
    line-height:40px !important;
}
.el-submenu .el-menu-item{
    color:#666;
    font-size:12.5px;
    height:40px;
    line-height:40px;
    padding:0;
    min-width:139px;
}
.el-submenu .el-menu-item:first-child{
    margin-top:-14px;
}
.el-menu-item.is-active{
    background:#409EFF;
    color:#fff;
}
</style>



